@media screen and (max-width: 1855px) {
  :root {
    font-size: 13.333px;
  }
  .container {
    width: 123.75rem;
  }
}
@media screen and (max-width: 1600px) {
  :root {
    font-size: 12px;
  }
}
@media screen and (max-width: 1440px) {
  :root {
    font-size: 10px;
  }
}
@media screen and (max-width: 1200px) {
  :root {
    font-size: 8.6px;
  }
  .container {
    width: 191.86rem;
  }
  .navbar-toggler {
    width: 70px;
    height: 40px;
    .navbar-toggler-icon {
      font-size: 16px;
    }
  }

  .navbar-collapse {
    display: flex;
    flex-direction: column-reverse;
    justify-content: flex-start;
    ul {
      position: relative;
      width: 100%;
      display: flex;
      // width: 100%;
      justify-content: center;
      margin: 0 !important;
      li {
        text-align: center;
      }
    }
    form {
      flex-flow: row nowrap;
    }
  }

  /// Vehicle grid
  //Product Grid
  #product .product-grid {
    height: auto;
    grid-template-rows: 300px 300px 450px;
    grid-template-columns: 425px 425px;
    grid-template-areas:
      "b2  b3"
      "b9  b5"
      "b6  b6";
  }
  //QuickComp
  .quick-comp .parent-box-com {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .prod-comp-div {
      width: 100% !important;
    }
  }

  //Product detail
  .prod-det {
    height: 25vh !important;
  }
}
@media screen and (max-width: 991px) {
  :root {
    font-size: 15px;
  }
  .col-sm-8 {
    width: auto !important;
  }
  .container {
    max-width: 60rem;
  }

  //hERO Section
  .home-banner {
    // height: 100vh;
    height: auto;

    .row {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .home-hero-img-wrapper {
        width: 100%;
        height: 40rem;
        margin-bottom: 0.5rem;
        .prod-tag {
          padding: 20px;
        }
      }

      .home-ch-row {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 100%;
        .home-hero-2-img-wrapper {
          height: 22.5rem;
          // width: 29.7rem;
          flex-basis: 49.5%;
        }
      }
    }
  }

  //Hot News section
  .hot-news {
    height: auto;
    .bread-crumb-title {
      font-size: $display-6;
    }
    .left-news-a {
      padding-bottom: 20px;

      .card {
        display: flex;
        // flex-direction: row;
        justify-content: space-between;
        width: 100%;

        .card-title {
          font-size: $display-3;
          font-weight: $font-weight-semibold;
        }
        .card-text {
          font-size: $display-8;
        }
      }
    }
    .left-news-b {
      padding-left: 0;
      .card {
        .hot-news-card-img-wrapper {
          width: 13rem;
          height: 100%;
          img {
            height: auto;
          }
        }
        .card-title {
          font-size: $display-6;
        }
        .card-text {
          font-size: $display-8;
        }
      }
    }
    .side-news-resp {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-top: 1.5rem;
      padding: 0 !important;
      .video-session {
        flex-basis: 49%;
      }
      .follow-us-session {
        flex-basis: 49%;
      }
    }
  }

  //Trending Seciton
  .trending {
    width: 100%;
    .trend-container {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .card-left {
        width: 100%;
        height: 33.33rem;
        margin-bottom: 7px;
        .prod-tag {
          padding: 10px;
        }
      }
      .two {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        height: auto;
        .first {
          display: none;
        }
        .second {
          display: flex;
          justify-content: space-between;
          height: 23rem;
          margin-top: 0;
          width: 100%;

          .card-right-bot {
            flex-basis: 49.5%;
            // height: inherit;
          }
        }
      }
    }
  }

  //Top Featured
  section.top-featured {
    // .featured-cards {
    //   .card-body {
    //     padding: 15px 0 0 10px !important;
    //   }
    // }
    .side-feature {
      display: flex;
      flex-direction: row;
      padding-left: 0 !important;
      justify-content: space-between;

      .side-advertise {
        margin-top: 50px;
        // flex-basis: 49%;
        order: 2;
      }
      .hot-div {
        // width: 28rem;
        // flex-basis: 49%;
        flex-direction: flex-start;
        order: 1;
      }
    }
  }

  // Freq Session
  section.freq-ask-ques {
    .card-body {
      font-size: $display-4;
    }
    .side-fnq {
      display: flex;
      flex-direction: row;
      padding-left: 0 !important;
      justify-content: space-evenly;
      margin-top: 20px;
      .quad-row {
        width: 24.3rem;
        .card {
          width: 10rem;
        }
      }
      .side-advertise-fnq {
        margin-top: 20px;
      }
    }
  }

  //Video session
  .video-session {
    height: 350px;
  }
  .follow-us-session {
    width: 100% !important;
    height: 350px !important;
  }

  //Veh grid section
  .veh-img {
    flex-basis: 40%;
    height: 130px !important;
  }
  .veh-cont {
    flex-basis: 60%;
  }
  //Product Grid
  #product .product-grid {
    height: auto;
    grid-template-rows: repeat(5, 300px);
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-template-areas:
      "b2"
      "b5"
      "b6"
      "b3"
      "b9";
  }

  //QuickComp
  .quick-comp .parent-box-com {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .prod-comp-div {
      width: 100% !important;

      .prod-comp-com {
        .prod-comp-o {
          .b-img {
            width: 17rem;
            height: 17rem;
          }
        }
      }
    }
  }

  //FixPadding
  .fix-padding {
    padding: 0 !important;
  }
  //Prod list div
  // .prod-desc a .prod-list-item {
  //   width: 39rem;
  //   height: 30rem;
  // }
  // .prod-desc {
  //   width: 100%;
  //   height: 25rem;
  // }
}

@media screen and (max-width: 768px) {
  :root {
    font-size: 12.4px;
  }

  .container {
    width: 53.2rem;
  }

  .hot-news {
    .left-news-b {
      display: flex;
      flex-direction: column;
    }
    .side-news-resp {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-top: 1.5rem;
      padding: 0 !important;
      .video-session {
        width: 100%;
      }
      .follow-us-session {
        flex-basis: 100%;
      }
    }
  }

  //Hot
  .side-feature {
    width: 100% !important;
    height: auto !important;
    display: flex !important;
    flex-direction: column !important;
    .side-advertise {
      width: 100% !important;
      height: 300px;
    }
    .hot-div {
      width: 100% !important;
    }
  }

  //QuickComp
  .quick-comp .parent-box-com {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .prod-comp-div {
      width: 100% !important;

      .prod-comp-com {
        .prod-comp-o {
          .b-img {
            width: 17rem;
            height: 17rem;
          }
        }
      }
    }
  }

  //Product detail
  .prod-det {
    height: 38vh !important;
  }
  //Footer
  footer .container .sections ul li a {
    font-size: 11px !important;
  }
  footer .container .sections .card .card-body {
    padding: 8px 12px !important;
  }
  footer .container .sections .card .f-thumb {
    width: 5rem !important;
    height: 5rem !important;
  }
}

@media screen and (max-width: 420px) {
  :root {
    font-size: 8.5px;
  }
  //Treding 22.3
  .container {
    // max-width: 43.5rem;
    width: 100%;
    padding: 0 1rem;
  }
  //Padding of card
  .card-body {
    padding: 7px !important;
  }
  //padding of .left-news-b
  .left-news-b {
    padding-left: 0 !important;
  }

  //hot news card img wrapper
  .hot-news-card-img-wrapper {
    padding-right: 1rem !important;
  }

  ///Side fnq
  .side-fnq {
    flex-direction: column !important;
    .quad-row {
      width: 100% !important;
      .f-div {
        .card {
          flex-basis: 48% !important;
        }
      }
    }
    .side-advertise-fnq {
      width: 100% !important;
    }
  }
  // card img top fnq
  .card-img-top-fnq {
    width: 100%;
    height: 10rem !important;
  }

  // .grid2 {
  //   height: auto;
  //   grid-auto-rows: 300px;
  //   grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  //   grid-template-areas:
  //     "a"
  //     "b"
  //     "c"
  //     "d";
  // }

  //Veh Grid Cont
  .veh-img {
    flex-basis: 40%;
    height: 100px !important;
  }
  .veh-cont {
    flex-basis: 60%;
  }

  //Prod List session
  #prod-sec {
    width: 100%;
    height: auto;
  }
  .prod-desc-grid {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    align-content: center;
  }
  .prod-desc {
    margin-bottom: 5rem !important;
  }
  .prod-list-item {
    width: 32rem !important;
    height: 28rem !important;
  }

  //Prod Description
  //Product detail
  .prod-det {
    height: auto !important;
  }
  .prod-disp-img {
    width: 100%;
    height: 40rem !important;
  }

  ///Product Label
  #top-desc .prod-label {
    display: none !important;
  }

  // Footer
  footer .container .sections .card .f-thumb {
    width: 12rem !important;
    height: 9rem !important;
  }
  // footer .container .sections .card {
  //   // align-content: center;
  //   // align-items: center;
  // }
  footer .container .sections ul li a {
    font-size: 14px !important;
  }
}

