// hero grid4

/* Grid 4  */
.grid4 {
  height: 80vh;

  display: grid;
  grid-gap: 1rem;
  grid-template-rows: repeat(2, 50%);
  grid-template-columns: auto 300px;
  grid-template-areas:
    "aa aa bb"
    "aa aa cc";
}
.grid-aa {
  grid-area: aa;
}
.grid-bb {
  grid-area: bb;
}
.grid-cc {
  grid-area: cc;
}
@media screen and (max-width: 1200px) {
  .grid4 {
    height: auto;
    grid-template-rows: repeat(3, 300px);
    // grid-auto-rows: 300px;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-template-areas:
      "aa"
      "bb"
      "cc";
  }
}

// Hot news
.hot-news {
  height: auto;

  .left-news-b {
    // padding-left: 20px;
    .card {
      overflow: hidden;
      .hot-news-card-img-wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          height: inherit;
        }
      }
      .card-body {
        width: 100%;
        overflow: hidden;
      }
    }
  }
}

//Top Featured
section.top-featured {
  .featured-cards {
    display: flex;
    flex-direction: column;
    .card {
      width: 25rem;
      height: 15rem;
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: inherit;
      }
    }
  }
  .side-feature {
    width: 25rem;
    height: auto !important;
    display: flex;
    flex-direction: column;
    .side-advertise {
      width: inherit;
    }
    .hot-div {
      width: inherit;
    }
  }
}

//Frequently Asked Question Session
section.freq-ask-ques {
  h3 {
    font-weight: $font-weight-bold !important;
  }
  .card {
    margin-bottom: 1.5rem;
    .card-header {
      background: $fnq-bg;
      border-left: 5px solid $brand;
      margin-bottom: 20px;
      h2 {
        .btn-link {
          font-size: 1.25rem !important;
          color: $brand !important;
          font-weight: $font-weight-semibold;
          text-decoration: none;
        }

        .btn-link:after {
          content: "\2212";
          width: 35px;
          font-size: 1.875rem;
          text-align: center;
          right: 20px;
          top: 8px;
          position: absolute;
        }
        .btn-link.collapsed:after {
          content: "\002B";
        }
        .btn-link.collapsed:before {
          // visibility: hidden;
          height: 0;
          width: 0;
        }
        .btn-link:before {
          content: "";
          height: 2.5rem;
          width: 2.5rem;
          top: 3rem;
          position: absolute;
          background: $fnq-bg;
          z-index: 1;
          transform: rotate(45deg);
          transition: 0.3s ease-in-out;
          // visibility: visible;
        }
      }
    }
    .card-body {
      padding: 2rem 4rem;
      text-align: justify;
    }
  }

  .side-fnq {
    display: flex;
    flex-direction: column;

    .quad-row {
      // display: flex;
      // flex-direction: column;
      .f-div {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 5px;

        .card {
          flex-basis: 45%;
          height: auto;
          // margin: 0 auto 0 0;

          .card-img-top-fnq {
            width: 100%;
            height: 8rem;

            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
          }
          p {
            line-height: 10px;
          }
        }
      }
    }

    .side-advertise-fnq {
      width: 24.3rem;
      height: 25rem;
      img {
        height: inherit;
      }
    }
  }
}
