.trending {
  margin-top: 6rem !important;
}

.grid2 {
  height: 80vh;
  margin: auto;

  display: grid;
  grid-gap: 1rem;

  /* explicit grid  */
  /* grid-template-areas  */
  grid-template-areas:
    "a a b b"
    "a a c d";
  grid-template-rows: repeat(2, 50%);
  grid-template-columns: auto auto;
}

.grid-cont {
  position: relative;
  /* width: 100%;
  height: 100%; */
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.grid-cont::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50%;
  background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.5),
    rgba(0, 0, 0, 0.1)
  );
}
.grid-flat {
  grid-column: span 2 / auto;
}
.grid-a {
  grid-area: a;
}
.grid-b {
  grid-area: b;
}
.grid-c {
  grid-area: c;
}
.grid-d {
  grid-area: d;
}

@media screen and (max-width: 1200px) {
  .grid2 {
    height: auto;
    // grid-auto-rows: 300px;
    grid-template-rows: repeat(4, 300px);
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-template-areas:
      "a"
      "b"
      "c"
      "d";
  }
}
