#contact-page {
  margin-top: 6rem;

  .input-form {
    width: 100%;
    padding: 3rem;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.16);
    textarea {
      resize: vertical;
    }
  }

  h3.details {
    margin: 6rem 6rem 4rem 6rem;
    padding: 0;
    position: relative;
    text-align: center;
  }
  h3.details::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 40%;
    height: 2px;
    background: #707070;
  }
  h3.details::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    width: 40%;
    height: 2px;
    background: #707070;
  }

  .address {
    width: 100%;
    height: auto;
    margin-bottom: 1rem;
    padding: 0 6rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .sub-ad {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      img {
        width: 30px;
        height: 30px;
        margin-right: 1.5rem;
      }
      .ad-data {
        display: flex;
        flex-direction: column;
        font-weight: 600;
      }
    }
  }
}

.my-btn {
  background-color: $brand;
  text-transform: uppercase;
  padding: 0.8rem 7rem;
  font-size: 1.1rem;
}
