* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  max-width: 103.125rem;
  padding: 0;
  // margin: 0;
}
.row,
.col,
.col-md-2,
.col-md-3,
.col-md-4,
.col-md-6,
.col-md-8,
.col-md-10,
.col-md-12,
.col-sm-2,
.col-sm-3,
.col-sm-4,
.col-sm-6,
.col-sm-8,
.col-sm-10,
.col-sm-12,
.col-lg-2,
.col-lg-3,
.col-lg-4,
.col-lg-6,
.col-lg-8,
.col-lg-10,
.col-lg-12 {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.card {
  padding: 0;
  margin: 0;
  border: none;
}
.card-body {
  .card-title,
  .card-text {
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
//Weights
.f-bold {
  font-weight: $font-weight-bold;
}
.f-semibold {
  font-weight: $font-weight-semibold;
}
.f-regular {
  font-weight: $font-weight-regular;
}

