}
$display-4: 1.5625rem;
//24
.display-5 {
  font-size: 1.5rem;
  color: $white;
}
$display-5: 1.5rem;
//20
.display-6 {
  font-size: 1.25rem;
  color: $white;
}
$display-6: 1.25rem;
//18
.display-7 {
