//search icon
.s-icon {
  font-size: 1.5rem;
  color: $brand;
}

//Tags

//bigTag
.tag1 {
  font-size: 10px;
  padding: 2px 1.25rem;
}

.wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    0deg,
    rgb(0, 0, 0.8),
    rgba(53, 53, 53, 0.7),
    rgba(211, 211, 211, 0.09),
    transparent,
    transparent
  );
}
//Inside Image Tag

.prod-tag {
  position: absolute;
  bottom: 0;
  left: 0;
  height: auto;
  color: $white;
  z-index: 5;
  .tag {
    display: inline;
    background-color: $brand;
    padding: 4px 15px;
  }
  .tag-title {
    // line-height: 20px;
    margin-top: 10px;
  }
  .tag-date {
    line-height: 20px;
    font-size: 12px;
  }
}

// Simple Tag
.tag-out {
  display: inline-block;
  // font-size: $copy-right;
  color: $white;
  background-color: $tag;
  padding: 5px 10px;
  margin-right: 10px;
}

//Small Cards
// .card

.bread-crumb {
  //   position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  // border-bottom: 10px solid $brand;
  box-shadow: 0 -10px 0 inset $brand;
  box-sizing: border-box;
  color: $white;
  font-size: $content;
  margin-bottom: 15px;
  .bread-crumb-title {
    padding: 7px 15px;
    display: inline-block;
    background-color: $brand;
  }
}

.sm-bred {
  box-shadow: 0 -8px 0 inset $brand;
  .sm-bred-title {
    padding: 2px 20px;
  }
}

section {
  margin-bottom: 7rem;
}

.side-advertise {
  height: 25.25rem;
  overflow: hidden;
  img {
    height: inherit;
  }
}

//Video Advertise
.video-session {
  width: 100%;
  height: 18.25rem;
  iframe {
    width: 100%;
    height: 100%;
  }
}

.side-news-resp {
  padding-left: 1.5rem;
  .follow-us-session {
    width: 25rem;
    background-color: $follow-us;
    padding: 20px 30px;
    border-top: 0.625rem solid $brand;
    h2 {
      line-height: 3rem;
      color: $brand;
    }
    p {
      text-align: justify;
    }
    .icons {
      color: $brand;
    }
  }
}

/// New Follow us
.new-follow-us {
  position: relative;
  width: 100%;
  background: #f7f7f7;
  padding: 1.8rem 2rem 3rem;
  span {
    color: $soft-text;
    padding-bottom: 1rem;
  }
  h3 {
    color: $brand;
    font-weight: bold;
  }

  .handle-grid {
    display: flex;
    flex-direction: column;
    .grid-info {
      // background-color: pink;
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      .info {
        // background: red;
        flex-basis: 80%;
        display: flex;
        justify-content: flex-start;
        img {
          width: 25px;
          height: 25px;
        }
        .handle {
          display: flex;
          flex-direction: column;
          h5 {
            color: $heading-color;
            font-size: 14px;
          }
          span {
            font-size: 10px;
          }
        }
      }
      .but {
        flex-basis: 20%;
        font-size: 1.2rem;
        text-align: center;
        // background: purple;
      }
    }
  }
}
.new-follow-us::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 10px;
  background: $brand;
}

.text-brand {
  color: $brand !important;
}
.text-heading {
  color: $heading-color;
}
.text-soft {
  color: $soft-text;
}
//margin section
.margin-5 {
  margin-top: 5rem;
}
.margin-6 {
  margin-top: 6rem;
}
.margin-7 {
  margin-top: 7rem;
}
.margin-8 {
  margin-top: 8 rem;
}
