#top-featured {
  margin-top: 6rem;

  .f-hero {
    position: relative;
    width: 100%;
    height: 80vh;
    .f-hero-img {
      width: inherit;
      height: inherit;

      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
    }
    .f-wrapper {
      position: absolute;
      //   top: 0;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 50%;
      background-image: linear-gradient(
        to top,
        rgba(0, 0, 0, 0.9),
        rgba(0, 0, 0, 0.7),
        rgba(0, 0, 0, 0.5),
        rgba(0, 0, 0, 0.3),
        rgba(0, 0, 0, 0)
      );
      //   background-color: rgba(255, 0, 0, 0.5);
    }

    .f-heading {
      .tag {
        font-size: 1.2rem;
      }
      h1 {
        font-size: 3rem;
      }
      .tag-date {
        font-size: 1.2rem;
      }
    }
  }
}
