#hot-news {
  margin-top: 6rem;
  .md-hot {
    margin-bottom: 30px;
    .hot-content-cont {
      width: 100%;
      height: 40rem;

      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;

      display: flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;
      img {
        width: inherit;
      }
    }
  }

  //Side Session
  .side-advertise {
    width: 25rem;
  }
  .hot-div {
    width: 25rem;
  }
}
