#top-desc {
  margin-top: 6rem;

  .prod-det {
    width: 100%;
    height: 53vh;
    // background-color: pink;
  }
  .prod-disp-img {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }
  .prod-disp-desc {
    padding: 1rem 0 0.5rem 3rem;
    .prod-disp-desc-label {
      display: flex;
      //   justify-content: center;
      align-items: center;
    }

    .book-btn {
      margin-top: 2rem;
      background-color: transparent;
      border: 1px solid #707070;
      font-size: 16px;
      padding: 0.3rem 1.5rem;
      transition: 0.3s ease;
    }
    .book-btn:hover {
      color: #fff;
      background-color: $brand;
      border: 1px solid #fff;
      transform: scale(1.1);
      transform: translateX(-10%);
    }
    .c1,
    .c2,
    .c3 {
      width: 30px;
      height: 30px;
      margin-top: 0.5rem;
      margin-right: 1rem;
    }
    .c1 {
      background-color: red;
    }
    .c2 {
      background-color: blue;
    }
    .c3 {
      background-color: black;
    }
  }

  .prod-label {
    margin-top: 5rem;
    width: 100%;
    height: auto;
    padding: 3rem 5rem;
    background: $light-brand;
    display: flex;
    justify-content: space-between;
    .inner-prod-label {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
    }
  }
}

// retailers
.retail-list {
  width: 100%;
  height: auto;
  padding: 2rem;
  background: $fnq-bg;
  ul {
    list-style: none;
    li {
      line-height: 3rem;
      a {
        font-size: 1.5rem;
      }
    }
  }
}
.popularcomp-list {
  @extend .retail-list;
  background: $light-brand;
}
