:root {
  --quote: ;
}
#news-session {
  margin-top: 7rem;

  .news-cont {
    width: 100%;
    height: 42rem;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    a {
      width: 100%;
    }
  }

  .header {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .quote {
      flex-basis: 8%;

      img {
        height: inherit;
        margin-right: 2rem;
      }
    }
    .header {
      flex-basis: 88%;
    }
  }

  .news-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: justify;
    padding: 1rem 2rem 1rem 0;
    line-height: 3rem;
  }
}
