#product {
  margin: 6rem 0;

  .product-grid {
    width: 100%;
    height: 82vh;
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: 300px auto auto 300px;
    grid-template-rows: repeat(2, 50%);
    grid-template-areas:
      "b2 b5 b5 b3"
      "b9 b5 b5 b6";

    .prod-item {
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
    .prod-item:nth-child(1) {
      grid-area: b2;
    }
    .prod-item:nth-child(2) {
      grid-area: b3;
    }
    .prod-item:nth-child(3) {
      grid-area: b5;
    }
    .prod-item:nth-child(4) {
      grid-area: b6;
    }
    .prod-item:nth-child(5) {
      grid-area: b9;
    }
  }
}

#prod-sec {
  width: 100%;
  height: 50vh;
}
.prod-desc-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: space-between;
}
.prod-desc {
  flex-basis: 31%;
  height: 250px;
  text-align: center;
  margin-bottom: 9rem;
  a {
    height: inherit;
    width: inherit;
    //   background: red;
    .prod-list-item {
      width: 23rem;
      height: 23rem;
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
  }
}
.load-btn {
  display: block !important;
  margin: 3rem auto 8rem;
}
.quick-comp {
  width: 100%;
  height: auto;
  padding: 4rem;
  background: $light-brand;
  margin-bottom: 4rem;
  .parent-box-com {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    .prod-comp-div {
      flex-basis: 45%;
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-bottom: 4rem;
      .prod-comp-com {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        .prod-comp-o {
          display: flex;
          flex-direction: column;
          .b-img {
            width: 14rem;
            height: 14rem;
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
          }
        }
      }
      .comp-btn {
        display: inline-block;
        background: $brand !important;
        margin: auto;
        padding: 0.5rem 2rem;
      }
    }
  }
}
