.about-us {
  margin-top: 6rem;

  .empire {
    text-align: justify;
    h2 {
      color: $brand;
    }
    .quote-block {
      position: relative;
      width: 100%;
      display: flex;
      flex-direction: row;
      img {
        width: 60px;
        height: 60px;
      }
    }
  }
  .values {
    h2 {
      color: $brand;
    }
  }
}

.veh-grid {
  display: flex;
  flex-direction: column;
  width: 100%;
  .veh-card {
    display: flex;
    flex-direction: row;
    margin-bottom: 2rem;
    .veh-img {
      width: 200px;
      // flex-basis: 20%;
      height: 80px;

      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
    }
    .veh-cont {
      // flex-basis: 80%;
      .veh-date {
        font-size: 12px;
      }
    }
  }
}
