.tym-row {
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
  width: 100%;
  .tym-col {
    .card {
      height: 350px;

      overflow: hidden;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .card-img-top {
        width: 100%;
        height: 80%;

        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
      }
    }
  }
}
