footer {
  width: 100%;
  height: auto;
  background-color: $brand;
  color: $white;
  .container {
    .sections {
      // border-right: 1px solid #ccc;
      // overflow: hidden;
      padding: 20px;

      // .sections:last-child {
      //   border: none;
      // }
      h2 {
        margin-bottom: 20px;
      }
      ul {
        li {
          display: inline-block;

          a {
            display: flex;
            flex-direction: row;

            align-items: center;
            font-size: 14px;
            color: $white;
            white-space: nowrap;
            i {
              font-size: 18px;
            }
          }
        }
      }

      .card {
        background-color: transparent;
        border: 0px;
        color: $white;
        margin-bottom: 20px;
        .f-thumb {
          width: 12rem;
          height: 10rem;
          background-position: center;
          background-size: cover;
          background-repeat: no-repeat;
        }
        .card-body {
          padding: 15px !important;

          .card-title {
            // line-height: 5px;
            font-weight: $font-weight-semibold;
          }
          .card-text {
            font-size: 12px;
          }
        }
      }
    }
  }
  .copyright-container {
    background-color: rgba(0, 0, 0, 0.15);
    .copy-right {
      font-size: $copy-right;
    }
  }
}
